* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Playfair Display, serif;
}

.logo {
  font-size: 4rem;
  font-weight: 800;
}

main {
  height: 80vh;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-top: 4rem;
  display: flex;
}

.leng {
  width: 4.2rem;
  border: none;
  padding: .3rem;
  font-size: 1.3rem;
}

.options {
  height: 40vh;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  font-size: 1.7rem;
  display: flex;
}

.check {
  width: 1.5rem;
  height: 1.5rem;
}

label {
  margin-right: 4rem;
}

.btn {
  z-index: 1;
  cursor: pointer;
  padding: .2rem;
  font-size: 2.2rem;
  font-weight: 800;
  transition: all .5s;
  position: relative;
}

.btn:hover:after {
  width: 10rem;
}

.btn:after {
  content: "";
  width: 0;
  height: 10px;
  background: #000;
  transition: all .5s;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.generated--password {
  font-size: 2.2rem;
}

